cc.Class({
    extends: cc.Component,

    properties: {
		itemHeight:40,
		scrollView: {
			default: null,
			type: cc.ScrollView
		},
		spawnCount: 0, // 
		totalCount: 0, // 
		spacing: 0, // 
		data_list:null,
		all_num_node:cc.Node,
		use_num_node:cc.Node,
		left_num_node:cc.Node,
		invalid_num_node:cc.Node,
		pthis:null,
    },
    onLoad () {
	},
	// 
    initialize: function () {
		this.content = this.scrollView.content;
		this.items = []; // 
        this.updateTimer = 0;  
        this.updateInterval = 0.2;
		this.itemHeight = 40;
        // 
        this.lastContentPosY = 0; 
        // 
        this.bufferZone = this.spawnCount * (this.itemHeight + this.spacing) / 2;
		// 
        this.content.height = this.totalCount * (this.itemHeight + this.spacing) + this.spacing;
    	for (let i = 0; i < this.spawnCount; ++i) { // spawn items, we only need to do this once
			if(this.data_list.length <= i){
				break;
			}
    		let item = cc.instantiate(g_assets["record_item_layout"]);
            this.content.addChild(item);
            // itemcontentAnchor(0.5, 1)itemy
    		item.setPosition(0, -item.height * (0.5 + i) - this.spacing * (i + 1));
    		item.getComponent('record_item').init(i,this.data_list[i],this.pthis);
            this.items.push(item);
    	}
    },

    // itemScrollView
    getPositionInView: function (item) {
        let worldPos = item.parent.convertToWorldSpaceAR(item.position);
        let viewPos = this.scrollView.node.convertToNodeSpaceAR(worldPos);
        return viewPos;
    },
	 // item(spawnCounttotalCount)
    update: function(dt) {
        this.updateTimer += dt;
        if (this.updateTimer < this.updateInterval) {
            return; // we don't need to do the math every frame
        }
		if(this.items == null || this.items.length <= 0){
			return;
		}
        this.updateTimer = 0;
        let items = this.items;
        // contenty
        let isDown = this.scrollView.content.y < this.lastContentPosY;
        // 
        let offset = (this.itemHeight + this.spacing) * items.length;
        let newY = 0;

        // item
        for (let i = 0; i < items.length; ++i) {
            let viewPos = this.getPositionInView(items[i]);
            if (isDown) {
                // itemy
                newY = items[i].y + offset;
                // itemnewYcontent
                // itemoffsetitem
                if (viewPos.y < -this.bufferZone && newY < 0) {
                    items[i].setPositionY(newY);
					let item = items[i].getComponent('record_item');
                    let itemId = item.itemID - items.length; // update item id
                    item.init(itemId,this.data_list[itemId],this.pthis);
					cc.log("prev id:" + itemId);
                }
            } else {
                // itemy
                newY = items[i].y - offset;
                // itemnewYcontent
                // itemoffsetitem
                if (viewPos.y > this.bufferZone && newY > -this.content.height) {
                    items[i].setPositionY(newY);
                    let item = items[i].getComponent('record_item');
                    let itemId = item.itemID + items.length;
					item.init(itemId,this.data_list[itemId],this.pthis);
					cc.log("next id:" + itemId);
                }
            }
        }

        // lastContentPosY
        this.lastContentPosY = this.scrollView.content.y;
    },

	init_record_info(data,pthis){
		var self = this;
		this.pthis = pthis;
		this.all_num_node.getComponent("cc.Label").string = data["fangka_history"];
		this.use_num_node.getComponent("cc.Label").string = parseInt(data["fangka_history"]) - parseInt(data["fangka_num"]);
		this.left_num_node.getComponent("cc.Label").string = data["fangka_num"];
		this.invalid_num_node.getComponent("cc.Label").string = data["invalid_fangka"];
		var param = {
			"player_id":data["id"],
			"index":0,
			"length":this.totalCount
		};
		Servers.gameInfoProcess("getBuyFangkaList",param,function(data){
			self.data_list = data.msg;
			self.totalCount = self.data_list.length;
			if(self.data_list.length > 0){
				self.initialize();
			}
		});
	},
	clear_scroll_data(){
		if(this.items == null){
			return ;
		}
		for (let i = 0; i < this.items.length; i++) { // spawn items, we only need to do this once
			let item = this.items[i];
			item.removeFromParent();
			item.destroy();
    	}
	},
});
