cc.Class({
    extends: cc.Component,

    properties: {
		tip_label:cc.Label,	
		no_g:"",
		has_g:"",
		join_g:"",
		adding_g:"3-5\n 0317-5071648",
    },
	set_text(tid){
		if(tid == "no_g"){
			this.tip_label.string = this.no_g;
		}else if(tid == "has_g"){
			this.tip_label.string = this.has_g;
		}if(tid == "join_g"){
			this.tip_label.string = this.join_g;
		}if(tid == "adding_g"){
			this.tip_label.string = this.adding_g;
		}
	},
    onLoad () {
		
	},

    start () {

    },

    // update (dt) {},
});
